import { ModuleWithProviders } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";


import { AuthView } from "./auth.view";
import { AgentsView } from "./agents/agents.view";
import { AgentResolver } from "./agents/agent.resolver";
import { AgentDetailsComponent } from "./agents/agent-details/agent-details.component";

const AgentsRoutes:Routes = [
	{
		path: "",
		component: AuthView,
		data: {
			alias: "auth",
			displayName: "Auth",
		},
		children: [
			{
				path: "",
				component: AgentsView,
				redirectTo: "/agents",
				pathMatch: "full",
				children: [
					{
						path: "agents",
						component: AgentsListView,
						data: {
							alias: "agents",
							displayName: "Agents",
						},
						children: [
							{
								path: ":agent-slug",
								resolve: {
									app: AgentResolver,
								},
								data: {
									param: "slug",
									displayName: "App",
									title: "App",
								},
								component: AgentDetailsComponent,
							}
						]
					},
				]
			},
		]
	}
];

export const routing:ModuleWithProviders = RouterModule.forChild( AgentsRoutes );